import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
// import App from "./App";
import registerServiceWorker from "./registerServiceWorker";
import { HashRouter, Route, Redirect, Switch } from "react-router-dom";
import { Parallax } from "react-spring";
import PaginationDotsContainer from "./views/PaginationDotsContainer";
import Pages from "./Pages";
import { createStore } from "redux";
import { Provider } from "react-redux";
import appReducer from "./reducers/appReducer";

/* eslint-disable no-restricted-globals*/
screen &&
  screen.orientation &&
  screen.orientation.lock("landscape").catch(err => console.error(err));
/* eslint-enable */
const store = createStore(
  appReducer,
  window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
);

const Routes = () => (
  <Provider store={store}>
    <HashRouter>
      <Route
        render={({ location }) => (
          <div className="app-container">
            <Switch location={location}>
              <Parallax
                ref={ref => (this.parallaxRef = ref)}
                pages={3}
                config={{ tension: 100, friction: 20 }}
                horizontal
                scrolling={false}>
                <Route
                  exact
                  path={process.env.PUBLIC_URL + "/"}
                  render={() => (
                    <Redirect to={process.env.PUBLIC_URL + "/#/feed"} />
                  )}
                />
                <Route
                  path={process.env.PUBLIC_URL + "/#/alarms"}
                  render={() => (
                    <Pages activePage={0} parallaxRef={this.parallaxRef} />
                  )}
                />
                <Route
                  path={process.env.PUBLIC_URL + "/#/feed"}
                  render={() => (
                    <Pages activePage={1} parallaxRef={this.parallaxRef} />
                  )}
                />
                <Route
                  path={process.env.PUBLIC_URL + "/#/settings"}
                  render={() => (
                    <Pages activePage={2} parallaxRef={this.parallaxRef} />
                  )}
                />
              </Parallax>
            </Switch>
            <PaginationDotsContainer />
          </div>
        )}
      />
    </HashRouter>
  </Provider>
);

ReactDOM.render(<Routes />, document.getElementById("root"));
registerServiceWorker();



